import React, {Component} from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {actionCreator} from '../../../../actions';
import Radio from 'material-ui/RadioButton';

import * as types from '../../../../constants/actiontypes';

const styles = {
  tagRadio: {
    marginLeft: 5,
    marginRight: 15,
  },
  tagRadioArea: {
    display: 'flex'
    
  },

};

class RadioButton extends Component {

  componentDidMount() {
  }

  constructor(props, context) {
    super(props, context);
  }

  change = (tagId, status) => {
    this.props.clickChange(tagId, status, this.props.actionType);
  }

  render() {

    return (
        <div style={styles.tagRadioArea}>
          <Radio
            onCheck={(event) => this.change(this.props.radio.tag_id, 1)}
            name={this.props.name}
            label=""
            labelPosition="left"
            checked={this.props.radio.tag_score}
          />
          <Radio
            onCheck={(event) => this.change(this.props.radio.tag_id, 0)} 
            name={this.props.name}
            label=""
            labelPosition="left"
            checked={!this.props.radio.tag_score}
          />
        </div>
    );
  }
}
RadioButton.propTypes = {
    clickChange: PropTypes.func,
};

const mapStateToProps = state => ({
  
});

const mapDispatchToProps = dispatch => ({
    clickChange: (tagId, status, actionType) => {
      dispatch(actionCreator(actionType, [tagId, status]));
    },
});

module.exports = connect(mapStateToProps, mapDispatchToProps)(RadioButton);

export default RadioButton;



